<script>
import axios from "axios";
import Error from "./Error.vue";
import { mapGetters } from "vuex";
export default {
  name: "Posts",
  components: {
    Error,
  },
  data() {
    return {
      displayModal: false,
      description: " ",
      title: "",
      tags: "",
      loading: "",
      images: [],
      fileList: "",
      url: [],
    };
  },
  methods: {
    modelClose() {
      this.$refs["postmodel"].hide();
    },

    imageupload(images, fileList) {
      this.images = fileList;
      this.images.forEach((value, index) => {
        var indadd = window.URL.createObjectURL(fileList[index]);
        this.url.push(indadd);
        console.log(this.url);
      });

      console.log(fileList);
    },

    removeImage(index) {
      this.url.splice(index, 1);
    },

    async handalSubmit() {
      const postCreate = new FormData();
      this.images.forEach((value, index) => {
        var img = `medias`;
        var ind = `[${index}]`;
        postCreate.append(img + ind, value);
      });
      postCreate.append("description", this.description);
      postCreate.append("title", this.title);
      postCreate.append("tags", this.tags);
      try {
        this.loading = true;
        const response = await axios
          .post("posts", postCreate, {})
          .then((res) => {
            console.log(res);
            this.loading = false;
            this.$router.push("/");
          })
          .catch((err) => {
            setTimeout(
              function(scope) {
                scope.loading = false;
              },
              1000,
              this
            );
            console.log(err);

            alert("One Fild Is Requerd");
          });

        console.log(response);
      } catch (e) {
        this.error = "Error occurred!";
      }
    },
  },
  computed: {
    ...mapGetters({
      user: "user",
    }),
  },
};
</script>

